import React, { Component } from 'react';
import user from './../logo/user.svg';
import { NavLink} from 'react-router-dom';
import textlogo from './../logo/textlogo.svg';
import jeetaryellow from './../img/jeetaryellow.svg';
import herojeetarlogo from './../img/herojeetarlogo.svg';
import styled from 'styled-components';


class Navbar extends Component {
	state = {  }
	render() { 
		return (

				<nav>
					<ul className="container nav pt-3 px-2 px-md-0">
						<li className="nav-item me-sm-5">
								<NavLink to="/" exact={true} className="nav-link nav-left" activeClassName="active">jeetar</NavLink>
						</li>
						<li className="nav-item me-sm-5">
								<NavLink to='/jeetarhealth' className="nav-link nav-left" activeClassName="active">jeetarhealth</NavLink>
						</li>
						<li className="nav-item me-sm-5">
								<NavLink to='/jeetarbusiness' className="nav-link nav-left" activeClassName="active">jeetarbusiness</NavLink>
						</li>
						<li className="d-none d-sm-block nav-item  me-2 ms-auto nav-right">
							<span exact={true} className="nav-link"  data-bs-toggle="modal"  data-bs-target="#loginModal">Login</span>
						</li>
						<li className="d-none d-sm-block nav-item nav-right">
							<span className="nav-link" data-bs-toggle="modal"  data-bs-target="#signupModal"> <img src={user} className="nav__userIcon" /> Sign Up</span>
						</li>
					</ul>
					<div className=" header-image d-flex justify-content-center align-items-center py-3 d-block d-sm-none">
						<img src={herojeetarlogo} className="img-fluid" />
					</div>
				</nav>
				
		 );
	}
}

 
export default Navbar